<template>
  <div class="my">
    <BookTitle>我的主页</BookTitle>
    <p>登录成功</p>
    <p @click="Signout">退出登录</p>
    <hr />
    <div class="noe" v-if="account">
      <p>请保存好账号密码</p>
      <p>账号：{{ account.username }}</p>
      <p>密码：{{ account.password }}</p>
      <van-button
        style=" margin-top: 3vh;  border: none; background: #b93321;"
        round
        block
        type="primary"
        native-type="submit"
        @click="Delete"
        >已知悉
      </van-button>
    </div>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue";
import { mapState, mapMutations } from "vuex";
export default {
  components: {
    BookTitle,
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(["account"]),
  },
  methods: {
    ...mapMutations(["Modifyloginstatus", "Addaccount"]),
    Signout() {
      this.Modifyloginstatus(0);
      this.$router.push("/");
    },
    Delete() {
      this.Addaccount(null);
    },
  },
};
</script>

<style lang="less" scoped>
.my {
  p {
    text-align: center;
  }
  .noe {
    margin-top: 2vw;
  }
}
</style>
